body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    background: grey;
    line-height: 1.45;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }

.main-content {
    min-height: 100vh;
    margin-top: 3rem;
    width: 100%;
}

.portfolio {
    display: grid;
    height: 100%;
    grid-template-rows: repeat(6, 1fr);
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 30px;
    padding: 2% 5% 5% 5%;
}

.portfolio-item {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    opacity: 0.7;
    transition: all ease-in-out 300ms;
}

.medium {
    grid-row: span 2;
    grid-column: span 2;
}

.large {
    grid-row: span 3;
    grid-column: span 3;
}

.tall {
    grid-row: span 3;
    grid-column: span 2;
}

.wide {
    grid-row: span 2;
    grid-column: span 3;
}

img {
    width: 100%;
    height: 100%;
}

.portfolio-item:hover {
    opacity: 1;
    -webkit-transform: scale(1.05, 1.05);
            transform: scale(1.05, 1.05);
}

@media only screen and (max-width: 768px){
    .portfolio {
        display: block;
        padding: 2% 5% 5% 5%;
    }
    
    .portfolio-item {
        margin: 1rem;
    }
}

.content-wrapper {
    display: grid;
    height: 100%;
    grid-template-rows: repeat(6, 50px);
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 0.8rem;
    padding: 2% 5% 5% 5%;
    text-align: center;
}

.content {
    grid-row: span 2;
    grid-column: 2 / span 3;
}

.title {
    grid-row: 1;
    grid-column: 2 / span 3;
    font-size: 150%;
}
* {
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  height: 100vh;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

p {
  color: white; }

.component-wrapper {
  width: 100%;
  height: 2rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  background-color: #000411;
  margin: 0;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-justify-content: center;
          justify-content: center;
  position: fixed;
  top: 0;
  overflow: hidden;
  z-index: 1; }

.header {
  font-size: 1rem;
  font-family: "Helvetica Neue", Helvetica, sans-serif;
  color: white; }

.switch {
  position: relative;
  width: 2rem;
  height: 1rem;
  border-radius: 20%;
  transition: background-color 100ms ease-out;
  z-index: 1;
  background-color: grey;
  margin: 1rem; }

.switch:before, .switch:after {
  content: "";
  position: absolute;
  top: 0;
  background-color: inherit;
  border-radius: 50%;
  width: 1rem;
  height: 1rem;
  z-index: 2; }

.switch:before {
  left: 1.25rem; }

.switch:after {
  right: 1.25rem; }

.toggle-button {
  position: absolute;
  width: 1rem;
  height: 1rem;
  background-color: #353535;
  border-radius: 50%;
  transition: -webkit-transform 100ms ease-in-out;
  transition: transform 100ms ease-in-out;
  transition: transform 100ms ease-in-out, -webkit-transform 100ms ease-in-out;
  z-index: 3;
  border: 0.05rem solid #353535;
  top: -0.05rem; }

.toggle-button_position-left {
  -webkit-transform: translateX(-0.27rem);
  transform: translateX(-0.27rem); }

.toggle-button_position-right {
  -webkit-transform: translateX(1.25rem);
  transform: translateX(1.25rem); }

:root {
  font-size: 20px; }

